.SkillBlock{
    display: flex;
    padding: 6rem 5rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: stretch;
}
.SkillContainer{
    display: flex;
    padding: 0rem 2rem;
    flex-direction: column;
    align-items: flex-start;
    gap: 3rem;
    align-self: stretch;
}

.SkillList{
    display: flex;
    /* justify-content: center; */
    flex-wrap: wrap;
    gap:3rem;
    align-items: center;
    align-self: stretch;
}

@media screen and (max-width:790px) {
    .SkillBlock{
        padding: 4rem 1rem;
    }
    .SkillContainer{
        padding: 0rem;
    }
    .SkillList{
        justify-content: space-evenly;
    }
}