.Experience {
    display: flex;
    padding: 6rem 5rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: stretch;
    background: var(--gray-50, #F9FAFB);
}
.ExperienceContainer{
    display: flex;
padding: 0rem 2rem;
flex-direction: column;
justify-content: center;
align-items: center;
gap: 3rem;
align-self: stretch;
}

@media screen and (max-width:790px) {
    .Experience{
        padding: 4rem 1rem;
    }   
    .ExperienceContainer{
        padding: 0rem;
    } 
}
